import React, { Component } from "react";
// import Container from "../../components/ContainerComponents/Container";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import AddSchoolPositions from "../../components/SchoolPositions/AddSchoolPositions";
import { connect } from "react-redux";
import {
  SchoolData,
  Auth,
  Basic,
  // FC_GetSchoolsByDistrict,
  FC_GetAllPositions,
  FC_GetSchoolPositions,
  FC_AllSchoolHierarchyDetails,
  SchoolHierarchyStore,
} from "../../actions";
// import Alert from "../../components/Alert/Alert";
import { StoreState } from "../../reducers";
import {
  PositionsOnSchool,
  SchoolPositionsCombination,
} from "../Head-Teacher/Request/HeadTeacherRequestToDee";
import { SchoolPositionApiResponse } from "../../types/HeadTeacherRequesTypes";
import Loading from "../../components/Loading/Loading";

interface AssignSchoolPositionProps {
  auth: Auth;
  basic: Basic;
  FC_GetAllPositions: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
  schoolHierarchyStore: SchoolHierarchyStore;
  FC_AllSchoolHierarchyDetails: (
    admin: boolean,
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface AssignSchoolPositionState {
  loading_data: boolean;
  schoolPositionsShow: {
    school_code: string;
    school_id: string;
    school: SchoolData;
    school_positions: PositionsOnSchool[];
  } | null;
}

export class _AssignSchoolPosition extends Component<
  AssignSchoolPositionProps,
  AssignSchoolPositionState
> {
  constructor(props: AssignSchoolPositionProps) {
    super(props);

    this.state = {
      loading_data: false,
      schoolPositionsShow: null,
    };
  }
  componentDidMount = () => {
    // Check if positions Available
    if (this.props.basic.positions === null) {
      this.props.FC_GetAllPositions((status: boolean, msg: string) => {
        this.setState({ loading_data: true });
        if (status === true) {
          this.setState({ loading_data: false });
        }
        console.log(status);
      });
    }
    if (this.props.basic.academic_calender && this.props.auth.school) {
      FC_GetSchoolPositions(
        this.props.auth.school.school_code,
        this.props.basic.academic_calender.academic_year_id,
        (
          status: boolean,
          data: SchoolPositionsCombination | null,
          msg: string
        ) => {
          if (data !== null) {
            this.setState({ loading_data: true });
            if (status === true && this.props.auth.school !== null) {
              this.setState({
                schoolPositionsShow: {
                  school_code: this.props.auth.school?.school_code,
                  school_id: this.props.auth.school.school_code, //to be added on the profile
                  school_positions: data.school_positions,
                  school: {
                    school_id: this.props.auth.school.school_code,
                    school_code: this.props.auth.school.school_code,
                    school_name: this.props.auth.school.school_name,
                    school_category: this.props.auth.school.schoolcategory,
                    school_status: this.props.auth.school.schoolstatus,
                    region_code: this.props.auth.school.regioncode,
                  },
                },
              });
              this.setState({
                loading_data: false,
              });
            }
          }
        }
      );
    }
  };
  render() {
    if (
      this.state.loading_data === true ||
      this.props.basic.positions === null ||
      this.state.schoolPositionsShow === null
    ) {
      return (
        <DashboardContainer>
          <div className="mx-auto p-8 flex flex-col items-center justify-center">
            <div>Loading, please wait...</div>
            <Loading />
          </div>
        </DashboardContainer>
      );
    }
    return (
      <DashboardContainer>
        <div className="">
          {this.state.schoolPositionsShow !== null &&
            this.props.basic.positions !== null && (
              <AddSchoolPositions
                auth={this.props.auth}
                positions={this.props.basic.positions}
                schoolPositions={
                  this.state.schoolPositionsShow.school_positions
                }
                school={this.state.schoolPositionsShow.school}
                appendPositionsToSchool={(
                  schoolPositions: SchoolPositionApiResponse[]
                ) => {
                  if (
                    this.state.schoolPositionsShow &&
                    this.props.basic.academic_calender
                  ) {
                    FC_GetSchoolPositions(
                      this.state.schoolPositionsShow.school_code,
                      this.props.basic.academic_calender.academic_year_id,
                      (
                        status: boolean,
                        data: SchoolPositionsCombination | null,
                        msg: string
                      ) => {
                        if (data !== null) {
                          this.setState({ loading_data: true });
                          if (status === true) {
                            this.state.schoolPositionsShow &&
                              this.setState({
                                schoolPositionsShow: {
                                  school_code:
                                    this.state.schoolPositionsShow.school_code,
                                  school_id:
                                    this.state.schoolPositionsShow.school_id,
                                  school_positions: data.school_positions,
                                  school: this.state.schoolPositionsShow.school,
                                },
                              });
                            this.setState({
                              loading_data: false,
                            });
                          }
                        }
                      }
                    );
                  }
                }}
                removePositionsToSchool={(school_has_position_id: string) => {
                  if (
                    this.state.schoolPositionsShow &&
                    this.props.basic.academic_calender
                  ) {
                    FC_GetSchoolPositions(
                      this.state.schoolPositionsShow.school_code,
                      this.props.basic.academic_calender.academic_year_id,
                      (
                        status: boolean,
                        data: SchoolPositionsCombination | null,
                        msg: string
                      ) => {
                        if (data !== null) {
                          this.setState({ loading_data: true });
                          if (status === true) {
                            this.state.schoolPositionsShow &&
                              this.setState({
                                schoolPositionsShow: {
                                  school_code:
                                    this.state.schoolPositionsShow.school_code,
                                  school_id:
                                    this.state.schoolPositionsShow.school_id,
                                  school_positions: data.school_positions,
                                  school: this.state.schoolPositionsShow.school,
                                },
                              });
                            this.setState({
                              loading_data: false,
                            });
                          }
                        }
                      }
                    );
                  }
                }}
                // close={() => {}}
              />
            )}
        </div>
      </DashboardContainer>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const AssignSchoolPosition = connect(mapStateToProps, {
  FC_GetAllPositions,
  FC_AllSchoolHierarchyDetails,
})(_AssignSchoolPosition);
